import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, WindowComponent } from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { CLASS_NAME, DipamGiaSp } from '../../../../models/dtos/mlf/dipamGiaSp';
import { DIPAM_B_GIA_SP_ENUM_VALUES } from '../../../../models/enumerated/mlf/dipamBGiaSpEnum';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';
import CepLocalidadeIS from '../../../components/ger/cepLocalidadeIS/CepLocalidadeIS';

interface DipamGiaSpEditViewProps extends EditViewProps<DipamGiaSp> {}

const DipamGiaSpEditView: FC<DipamGiaSpEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const formProps = useForm<DipamGiaSp>({
    defaultValues: { ...selectedItem, empresa: globalReducer.globalParameter.empresa },
    mode: 'onBlur',
  });

  return (
    <WindowComponent
      title="DIPAM B - GIA SP"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter)]}
      wikiId={35}
    >
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <InputGroupInline id="empresa.id" formProps={formProps} hidden={true} />
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="referencia"
              isPrependLabel={true}
              label="Referência"
              type={InputType.REFERENCE}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="codigo"
              isPrependLabel={true}
              label="Código"
              placeholder="Selecione o código."
              inputDropDownProps={{
                optionsProvider: DIPAM_B_GIA_SP_ENUM_VALUES,
                selectProps: { isClearable: false },
              }}
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <CepLocalidadeIS formProps={{ ...formProps, validation: { required: true } }} id="localidade" />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="valor"
              placeholder="Valor"
              isPrependLabel={true}
              label="Valor"
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default DipamGiaSpEditView;
